import Scrollbar from 'smooth-scrollbar';
import { TweenLite, TimelineMax, Power4, Expo } from 'gsap';





$(document).ready(function() {
    $('.menu-icon').on('click', openNav);
    $('.close-icon').on('click', closeNav);
    loader();
    
        const scrollbar = Scrollbar.init(document.querySelector('#app'), {
            damping: 0.05,
            renderByPixels: true
        })

        function parallax() {
            requestAnimationFrame(parallax);
            let elems = $('.parallax');
            
            elems.each((index, elem) => {
                let strafe = elem.getAttribute('data-speed');
                let offsetY = scrollbar.offset.y / strafe;
                let transformString = `translate3d(0, ${offsetY}px, 0)`;
                let style = elem.style;
                style.transform = transformString;
                style.webkitTransform = transformString;
            })
    }
    
    function paginator() {
        requestAnimationFrame(paginator)
        let elems = $('.paginator');
        elems.each((index, elem) => {
            let offset = scrollbar.offset.y;
            if(checkDistance(elem, offset, 0)) {
                let number = elem.getAttribute('data-section');
                let offsetDot = (number * 320) - 320;
                $('.pagination-number_two').html(number);
                TweenLite.to($('.dot-active'), 1.3, {y: `${offsetDot}%`})
            }
        })
    }
    
    function checkDistance (block, offset, margin) {
        let elemTop = block.offsetTop;
        let distanceTop = offset - elemTop + margin;
        let distanceBottom = block.offsetHeight + elemTop - offset;
       
        return distanceTop >= 0 && distanceBottom >= 0; 
    }
    
    
    parallax();
    paginator();
   
  
   
    
})

let requestAnimationFrame = window.requestAnimationFrame || window.mozRequestAnimationFrame ||
window.webkitRequestAnimationFrame || window.msRequestAnimationFrame;

window.requestAnimationFrame = requestAnimationFrame;



function openNav() {
    let tlNav = new TimelineMax();
    tlNav
        .set($('.nav-mobile__item'), {y: 20, opacity: 0})
        .to($('.mobile'), 0.6, {y: "100%", ease: Power3.easeInOut})
        .staggerTo($('.nav-mobile__item'), 1, {y: 0, opacity: 1, ease: Power4.easeOut}, "0.09", "-=0.2")
        .to($('.close-icon'), 0.7, {scale: 1}, "-=0.9")
}

function closeNav() {
    let tlClose = new TimelineMax();
    tlClose
        .to($('.mobile'), 0.6, {y: "0%", ease: Power3.easeOut})
        .set($('.close-icon'), {scale: 0})
        .set($('.nav-mobile__item'),  {y: 20, opacity: 0,})
}

function loader() {
    let tlLoader = new TimelineMax();
    tlLoader
        .to($('.loader__overlay'), 0.7, {scaleX: 1})
        .set($('.loader__title'), {className: '+= active-label'})
        .set($('.loader__overlay'), {className: '+= change'})
        .to($('.loader__overlay'), 0.7, {scaleX: 0})
        .to($('.loader'), 0.6, {x: "100%", ease: Expo.easeIn})
        .staggerTo($('.header-content__title'), 1.7, {y: 0, ease: Expo.easeOut}, '0.1')
        .to([$('.menu-icon'), $('.menu-label'), $('.nav'), $('.info-label'), $('.language-label')], 1.4, {y: 0, ease: Expo.easeOut}, '-= 1.4')
        .to([$('.header-content__btn'), $('.header-content__icon-mouse'), $('.header-content__icon-arrow')], 1, {y: 0, opacity: 1, ease: Expo.easeOut}, '-= 1')
        .to($('.bottom-clip'), 1.6, {scaleX: 1, ease: Expo.easeOut}, "-=1")
}








